html{
    font-size: 20px;
}
* {
    box-sizing: border-box;
}
html,body{
    width: 100%;
    height: 100%;
}
.g-wrapper {
    position: relative;
    width: 100%;
    min-height: 100%;
    overflow-x: hidden;
    line-height: 1;
    background-color: #01b9ff;
    padding-top: .75rem;
    padding-bottom: 1.6rem;
}

.g-header {
    position: relative;
    width: 100%;
    height: 15.8rem;
    background: url(../img/banner.png) 0 0 no-repeat;
    background-size: contain;
    
    .u-title {
        position: absolute;
        top: 1.75rem;
        left: 50%;
        margin-left: -6.4875rem;
        width: 12.975rem;
        height: 4.75rem;
        background: url(../img/title.png) 0 0 no-repeat;
        background-size: contain;
    }
    
    .u-subtitle {
        position: absolute;
        top: 10.5rem;
        left: 2.1rem;
        width: 6.325rem;
        height: 1.675rem;
        background: url(../img/subtitle.png) 0 0 no-repeat;
        background-size: contain;
    }
}


.g-content {
    position: relative;
    z-index: 999;
    margin: -.825rem auto 0;
    width: 15rem;
    height: 44.5rem;
    background: url(../img/step.png) 0 0 no-repeat;
    background-size: contain;
}
